.title-font{
    font-family: "Comic Neue", "AoyagiKouzanTSubset", sans-serif;
}

#top {
    border-radius: 80%;
}
nav {
    --typography-spacing-vertical: 0rem;
    --nav-element-spacing-vertical: 0.25rem;
    --form-element-spacing-vertical: 0.25rem;
}
.wrapper {
    min-height: 100%;
}
header {
    position: sticky;
    top: 0;
    background-color: var(--background-color);
    box-shadow: 0 6px 10px -10px rgba(0,0,0,.7);
    margin-bottom: var(--spacing);
    z-index: 100 // dropddown が 99 なので, header は 100 にしておく.
}
.langselector {
    margin-left: auto;
}
#table-of-contents {
    align-items: center;
    justify-content: start;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    margin-bottom: 10px;
    padding-bottom: 10px;
    h2 {
        --font-size: 1rem;
        border: none;
    }
    h2:after {
        content: ":";
        padding: 0;
        margin: 0;
        margin-right: var(--nav-link-spacing-horizontal);
    }
    & ul li {
        &:not(:first-child) {
            margin-inline-start: var(--nav-link-spacing-horizontal);
        }
        &:not(:last-child) {
            ::after {
                position: absolute;
                width: calc(var(--nav-link-spacing-horizontal) * 2);
                margin-inline-start: calc(var(--nav-link-spacing-horizontal) / 2);
                content: "/";
                color: var(--muted-color);
                text-align: center;
                text-decoration: none;
            }
        }
    }
    & a[aria-current] {
        background-color: transparent;
        color: inherit;
        text-decoration: none;
        pointer-events: none;
    }
}

footer p{
    text-align: right;
}

.profile-picture {
    height: 20vw;
    max-height: 260px;
    width: 20vw;
    max-width: 260px;
    position: relative;
    float: right;
    border-radius: 50%;
    margin-top: -1rem;
}

/// custom breakpoints
.menu-btn {
    display: none;
}
@if map-get($breakpoints, "md") {
    @media (min-width: map-get($breakpoints, "md")) {
        .md-hide {
            display: none;
        }
        .back_to_top {
            margin-left: 85%;
        }
        .title-font {
            font-size: 3rem;
        }
    }
    @media (max-width: map-get($breakpoints, "md")) {
        // responsive menu
        .menu-icon {
            display: flex;
            height: 60px;
            width: 60px;
            justify-content: center;
            align-items: center;
            position: relative;
            z-index: 200;  // header is 100
            cursor: pointer;
        }
        .menu-icon span,
        .menu-icon span:before,
        .menu-icon span:after {
            content: '';
            display: block;
            height: 3px;
            width: 25px;
            border-radius: 3px;
            background: $black;
            transition: 0.5s;
            position: absolute;
        }
        .menu-icon span:before {
            bottom: 8px;
        }
        .menu-icon span:after {
            top: 8px;
        }
        #menu-btn:checked ~ .menu-icon span {
            background: $white;
        }
        #menu-btn:checked ~ .menu-icon span::before {
            bottom: 0;
            transform: rotate(45deg);
        }
        #menu-btn:checked ~ .menu-icon span::after {
            top: 0;
            transform: rotate(-45deg);
        }
        .menu {
            width: 100%;
            height: 100%;
            position: fixed;
            top: 0;
            left: 100%;
            z-index: 200;
            background: $white;
            transition: .5s;
            flex-direction: column;
            margin-top: 120px;
        }
        #menu-btn:checked ~ .menu {
            left: 0;
            width: 100%;
        }
        .title-font {
            font-size: 2rem;
        }
        #table-of-contents {
            display: none;
        }
        .sm-hide {
            display: none;
        }
        .profile-picture {
            margin-top: -4rem;
        }
    }
}
