.back_to_top {
    --offset: 80px;
    position: sticky;
    bottom: 20px;
    margin-top: calc(100vh + var(--offset));
    margin-left: calc(100% - 20px - 6rem);
    text-decoration: none;
    padding: 10px;
    color: #fff;
    background: #000;
    border-radius: 100px;
    white-space: nowrap;

}
