span.anchor {
    display: none;
}
.sanchor {
    background: url(data:image/png;base64,);
    background-position: bottom right;
    background-repeat: no-repeat;
    padding: 14px 14px 0px 0px;
    color: $white;
    background-color: transparent;
    font-size: 1px;
}
.canchor {
    background:url(data:image/png;base64,);
    background-position: bottom right;
    background-repeat: no-repeat;
    padding: 12px 12px 0px 0px;
    color: $white;
    background-color: transparent;
    font-size: 1px;
}
.mail {
    display: none;
}
.sanchor:before {
    display: block;
    content: " ";
    margin-top: -110px;
    height: 110px;
    visibility: hidden;
}
p.source {
    text-align: right;
    font-size: 80%;
    cite {
        font-style: normal;
    }
}
.day {
    h2 { font-size: 1rem; }
}
.comment {
    display: none;
}

.tdiary-profile {
    width: 70%;
}
.tdiary-profile-picture {
    border-radius: 50%;
}
.footer{
    display: block;
    margin: 0 auto;
    text-align: right;
    font-size: 80%;
    width: 100%;
}
