@font-face {
  font-family: 'Comic Neue';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Comic Neue'), local('Comic Neue'),
       url('/assets/fonts/ComicNeueSubset.woff2') format('woff2');
}
@font-face {
  font-family: 'AoyagiKouzanTSubset';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('AoyagiKouzanTSubset'), local('AoyagiKouzanTSubset'),
       url('/assets/fonts/AoyagiKouzanTSubset.woff2') format('woff2');
}
