// kbd
kbd {
  background-color: var(--code-kbd-background-color);
  color: var(--code-kbd-color);
  vertical-align: baseline;
}

figure.highlight {
    margin: 0;
    padding: 0;
    border: 0;
    font-family: inherit;
}
